<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>行为检测</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-select size="small" v-model="formInline.deptName" placeholder="请选择">
          <el-option label="全部" value=""></el-option>
          <el-option v-for="(item, index) in deptNameInfoList" :key="index" :value="item">{{item}}</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.phone" placeholder="输入手机号"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.license_plate" placeholder="输入车牌号"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        <!--        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button>-->
        <!--        <el-button size="small" type="primary" @click="handleunit()">部门设置</el-button>-->
      </el-form-item>
    </el-form>

    <img :src="base64Image" height="600px" width="1067dp">
    <!--    <div class="block">-->
    <!--      <span class="demonstration">-->
    <!--         -->
    <!--      </span>-->
    <!--      <el-image :src="base64Image">-->
    <!--        <div slot="placeholder" class="image-slot">-->
    <!--          加载中<span class="dot">...</span>-->
    <!--        </div>-->
    <!--      </el-image>-->
    <!--    </div>-->
    <p>
      <el-tag>司机信息详细列表</el-tag>
    </p>

    <!--列表-->
<!--    <el-table size="small" @selection-change="selectChange" :data="visibleVideoInfoList" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">-->
    <el-table size="small" @selection-change="selectChange" :data="visibleTruckInfoList" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="selection" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="公司" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="username" label="用户名" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="phone" label="手机号" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="license_plate" label="车牌号" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="sex" label="性别" min-width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="status:" label="状态" min-width="120">
        <template slot-scope="scope">
          <el-tag :type="getStatusType(scope.row.status)">
            {{ getStatusText(scope.row.status) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="300">
        <template slot-scope="scope">
          <!--          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">查看</el-button>-->
          <el-button v-if="scope.$index==currentIndex" size="mini" type="danger" @click="stop(scope.$index, scope.row)">暂停</el-button>
          <el-button v-else size="mini" type="success" @click="detection(scope.$index, scope.row)">查看</el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)">联系</el-button>
          <!--          <el-button size="mini" type="success" @click="offlineUser(scope.$index, scope.row)">下线</el-button>-->
        </template>
      </el-table-column>

    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="currentTrackInfoList.length">
    </el-pagination>
  </div>
</template>

<script>
import Pagination from '../../components/Pagination'
import axios from "axios";
import car from '@/assets/img/car.jpg'

export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      loading: false, //是显示加载
      // 请求数据参数
      formInline: {
        deptName: '',//给出的限制条件
        phone: '',
        license_plate: '',
      },
      currentIndex: -1,
      base64Image: car,
      // 通信的端口
      IPaddress: '',
      WebSocketIP: '',
      socket: '',
      trackInfoList: [],
      currentTrackInfoList:[],
      deptNameInfoList:[],
    }
  },
  // 注册组件
  components: {
    Pagination
  },

  /**
   * 数据发生改变
   */
  watch: {},

  /**
   * 创建完毕
   */
  created() {
    this.trackList()
    this.getdata(this.formInline)
  },
  computed: {
    index() {
      return index
    },
    //模拟分页查询
    visibleTruckInfoList() {

      let result = this.trackInfoList;


      if (this.formInline.deptName) {
        // 根据部门名称进行筛选
        result = result.filter(video => video.deptName === this.formInline.deptName);
      }
      if (this.formInline.phone) {
        // 如果手机号不为空，则根据手机号进行筛选
        result = result.filter(video => video.phone === this.formInline.phone);
      }
      if (this.formInline.license_plate) {
        // 如果车牌号不为空，则根据车牌号进行筛选
        result = result.filter(video => video.license_plate === this.formInline.license_plate);
      }

      this.currentTrackInfoList=result;
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = this.currentPage * this.pageSize;
      return result.slice(startIndex, endIndex);
    },
  },
  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },

    deptNameList(){
      this.trackInfoList.forEach(obj => {
        if(!this.deptNameInfoList.includes(obj.deptName)) {
          this.deptNameInfoList.push(obj.deptName);
        }
      });
      console.log("this.deptNameInfoList.length"+this.deptNameInfoList.length+"this.deptNameInfoList"+this.deptNameInfoList[0])
    },

    // 点击暂停
    stop: function () {
      if (this.socket.readyState === WebSocket.OPEN)
        this.socket.close();
      this.currentIndex=-1;
    },
    // 点击播放
    detection: function (index, row) {
      console.log("row.status:"+row.status)
      if (row.status=='0'){
        this.$message.error('车辆在停止中无法查看!!');
        return;
      }
      this.currentIndex=index;
      this.IPaddress = row.ipaddress
      console.log("this.IPaddress:" + this.IPaddress)
      this.WebSocketIP = "ws://" + this.IPaddress + ":6010/websocket";
      console.log("this.WebSocketIP:" + this.WebSocketIP)
      if (this.socket.readyState === WebSocket.OPEN)
        this.socket.close();
      this.socket = new WebSocket(this.WebSocketIP);
      this.socket.onopen = function () {
        // Web Socket 已连接上，使用 send() 方法发送数据
        this.socket.send("发送数据")
        alert("数据发送中...")
      }

      // 监听WebSocket消息
      this.socket.onmessage = (event) => {
        console.log("event.data:" + event.data);
        console.log("event.data:" + event.data.toString());
        console.log("this.videoContent:");

        const base64Data = event.data; // 将这里替换为你的Base64编码图像数据
        this.base64Image = 'data:image/jpg;base64,' + base64Data;

      };
    },
    //搜索事件
    search() {

    },
    getStatusText(status) {
      return status == 0 ? '停止中' : status == 1 ? '驾驶中' : '异常中';
    },
    getStatusType(status) {
      return status == 0 ? 'info' : status == 1 ? 'success' : 'danger';
    },
    trackList() {
      console.log("后端数据刷新！！！")
      this.loading = true
      var that = this
      axios({
        method: 'post',
        url: '/Track/get_track_info_list',
      }).then((response) => {
        if (response.data != null) {
          that.trackInfoList = response.data
          that.deptNameList();
        } else {
          console.log("提取失败！")
          //失败
          this.$message({
            type: 'error',
            message: '提取失败！'
          })
        }
      })
      this.loading = false
    },
    // 选择复选框事件
    selectChange(val) {
      this.selectdata = val
    },
  },

  mounted() {
    // 测试用例
    // this.WebSocketIP = "ws://192.168.29.76:6010/websocket";
    // console.log("this.WebSocketIP:" + this.WebSocketIP)
    // if (this.socket.readyState === WebSocket.OPEN)
    //   this.socket.close();
    // this.socket = new WebSocket(this.WebSocketIP);
    // this.socket.onopen = function () {
    //   // Web Socket 已连接上，使用 send() 方法发送数据
    //   this.socket.send("发送数据")
    //   alert("数据发送中...")
    // }
    //
    // // 监听WebSocket消息
    // this.socket.onmessage = (event) => {
    //   console.log("event.data:" + event.data);
    //   console.log("event.data:" + event.data.toString());
    //   console.log("this.videoContent:");
    //
    //   const base64Data = event.data; // 将这里替换为你的Base64编码图像数据
    //   this.base64Image = 'data:image/jpg;base64,' + base64Data;
    //
    // };

    // 建立WebSocket连接到后端
    // console.log("mounted()函数中的WebSocketIP:"+this.WebSocketIP)
    // // ws://192.168.29.76/websocket
    // // const socket = new WebSocket('ws://192.168.43.216:6010/websocket');
    // this.socket = new WebSocket(this.WebSocketIP);
    // // const socket = new WebSocket('ws://192.168.29.76:6010/websocket');
    // // 发送消息
    // this.socket.onopen = function () {
    //   // Web Socket 已连接上，使用 send() 方法发送数据
    //   this.socket.send("发送数据")
    //   alert("数据发送中...")
    // }
    //
    // // 监听WebSocket消息
    // this.socket.onmessage = (event) => {
    //   console.log("event.data:" + event.data);
    //   console.log("event.data:" + event.data.toString());
    //   console.log("this.videoContent:");
    //
    //   const base64Data = event.data; // 将这里替换为你的Base64编码图像数据
    //   this.base64Image = 'data:image/jpg;base64,' + base64Data;
    //
    // };
  },
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}

.userRole {
  width: 100%;
}
</style>




