<template>
  <div class="login-wrap">
    <el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px"
             class="demo-ruleForm login-container">
      <h3 class="title">用户登录</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="ruleForm.username" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="ruleForm.password" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <!--      图形验证-->
      <el-row>
        <el-col :span="12">
          <el-form-item prop="code">
            <el-input type="text" v-model="ruleForm.code" auto-complete="off" placeholder="图形验证码"
                      @keyup.enter.native="submitForm('ruleForm')"></el-input>
          </el-form-item>
        </el-col>
        <el-col  :span="12" class="code-box">
          <div class="login-code" width="100%" @click="refreshCode">
            <!--验证码组件-->
            <v-sidentify></v-sidentify>
          </div>
          <!--          <img :src="ruleForm.codeimg" alt="" class="codeimg" @click="getcode()">-->
        </el-col>
      </el-row>
      <el-checkbox class="remember" v-model="rememberpwd">记住密码</el-checkbox>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')" :loading="logining">登录
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
import axios from "axios";
import {setCookie, getCookie, delCookie} from '../utils/util'
import md5 from 'js-md5'
import Sidentify from "../components/v-sidentify.vue";

export default {
  name: 'login',
  data() {
    return {
      //定义loading默认为false
      logining: false,
      // 记住密码
      rememberpwd: false,
      identifyCodes: "1234567890abcdefjhijklinopqrsduvwxyz", //随机串内容,从这里随机抽几个显示验证码
      identifyCode: "", //验证码图片内容
      ruleForm: {
        //username和password默认为空
        username: '',
        password: '',
        code: '',
        randomStr: '',//
        codeimg: '',//
        sidentify: '',
      },
      //rules前端验证:rules="rules" 的作用就是将这个表单的验证规则与 rules
      // 数据属性绑定在一起，使得当表单的某个字段不符合对应的验证规则时，可以显示对应的错误消息。
      rules: {
        username: [{required: true, message: '请输入账号', trigger: 'blur'}],
        password: [{required: true, message: '请输入密码', trigger: 'blur'}],
        code: [{required: true, message: '请输入验证码', trigger: 'blur'}]
      }
    }
  },
  components: {
    'v-sidentify': Sidentify
  },
  // 创建完毕状态(里面是操作)
  created() {

    this.$message({
      message: '账号密码及验证码不为空即可',
      type: 'success'
    })
    // 获取图形验证码
    this.createCode()
    // 获取存在本地的用户名密码
    this.getuserpwd()

  },
  mounted() {
    // 初始化验证码
    this.identifyCode = "";
    //参数：（1）随机串内容。（2）验证码显示位数
    this.makeCode(this.identifyCodes, 4);
  },
  // 里面的函数只有调用才会执行
  methods: {

    // 重置验证码
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    //获取验证码的值
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        //通过循环获取字符串内随机几位
        this.identifyCode +=
          this.identifyCodes[this.randomNum(0, this.identifyCodes.length)];
      }
    },
    //随机数字：用于当角标拿字符串的值
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },

    //
    // getcode() {
    //   // 生成一个新的验证码
    //   this.ruleForm.code = Math.random().toString(36).substr(2, 4);
    //   // 更新验证码图片的 src
    //   this.ruleForm.codeimg = 'data:image/png;base64,' + btoa(this.ruleForm.code);
    // },
    // 获取保存在本地的用户名密码
    getuserpwd() {
      if (getCookie('user') != '' && getCookie('pwd') != '') {
        this.ruleForm.username = getCookie('user')
        this.ruleForm.password = getCookie('pwd')
        this.rememberpwd = true
      }
    },
    //获取info列表
    // 提交名为formName的表单，并使用该表单的验证函数进行验证。如果表单验证成功，则执行回调函数；如果验证失败，则不执行回调函数。
    submitForm(formName) {
      //$refs是一个特殊的属性，用于直接访问模板中的 HTML 元素或子组件。这里，this.$refs[formName]可能是在引用名为formName的表单元素。
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logining = true
          // 测试通道，不为空直接登录
          setTimeout(() => {
            this.logining = false
            this.$store.commit('login', 'true')
            this.$router.push({path: '/goods/Goods'})
          }, 1000)
        }
          // 注释
          login(this.ruleForm).then(res => {
            if (res.success) {
              if (this.rememberpwd) {
                //保存帐号到cookie，有效期7天
                setCookie('user', this.ruleForm.username, 7)
                //保存密码到cookie，有效期7天
                setCookie('pwd', this.ruleForm.password, 7)
              } else {
                delCookie('user')
                delCookie('pwd')
              }
              //如果请求成功就让他2秒跳转路由
              setTimeout(() => {
                this.logining = false
                // 缓存token
                localStorage.setItem('logintoken', res.data.token)
                // 缓存用户个人信息
                localStorage.setItem('userdata', JSON.stringify(res.data))
                this.$store.commit('login', 'true')
                this.$router.push({ path: '/goods/Goods' })
              }, 1000)
            } else {
              this.$message.error(res.msg)
              this.logining = false
              return false
            }
          })
        //   var that = this
        //   axios({
        //     method: 'post',
        //     url: '/User/get_track_info_list',
        //   }).then((response) => {
        //     if (response.data != null) {
        //       if (this.rememberpwd) {
        //         //保存帐号到cookie，有效期7天
        //         setCookie('user', this.ruleForm.username, 7)
        //         //保存密码到cookie，有效期7天
        //         setCookie('pwd', this.ruleForm.password, 7)
        //       } else {
        //         delCookie('user')
        //         delCookie('pwd')
        //       }
        //
        //
        //     } else {
        //       this.$message.error(res.msg)
        //       this.logining = false
        //       return false
        //     }
        //   })
        // } else {
        //   // 获取图形验证码
        //   this.getcode()
        //   this.$message.error('请输入用户名密码！')
        //   this.logining = false
        //   return false
        // }
      })
    },
  }
}
</script>

<style scoped>
.login-wrap {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding-top: 10%;
  background-image: url(data:image/svg+xml;base64,);
  /* background-color: #112346; */
  background-repeat: no-repeat;
  background-position: center right;
  background-size: 100%;
}

.login-container {
  border-radius: 10px;
  margin: 0px auto;
  width: 350px;
  padding: 30px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  text-align: left;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
}

.title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}

.remember {
  margin: 0px 0px 35px 0px;
}

.code-box {
  text-align: right;
}

.codeimg {
  height: 40px;
}
</style>



