<template>
  <div style="width:100%;height:100%;">
    <div class="container">
      <!--      -->
      <div class="search-box">
        <el-autocomplete
          style="width: 300px"
          clearable
          auto-complete="off"
          v-model="licensePlate"
          :fetch-suggestions="querySearchAsync"
          @select="handleSelect"
          placeholder="请输入车牌号进行查询"
        ></el-autocomplete>
        <el-button type="primary" icon="el-icon-search" :loading="loading" @click="searchAddress">搜索</el-button>

<!--        输入地址-->
        <el-autocomplete
          clearable
          auto-complete="off"
          style="width: 300px"
          v-model="searchValue"
          :fetch-suggestions="searchInput"
          placeholder="请输入位置关键字"
          @select="selectLocation"
        ></el-autocomplete>
        <el-button type="primary" icon="el-icon-search" @click="goToSelectedLocation">搜索</el-button>

      </div>
      <div id="container" style="width: 100%; height: 100%" >

      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import {trackList} from "../../api/amapMG";

window._AMapSecurityConfig = {
  securityJsCode: '97cbc1db59da0e4687d4dbcf41484d78' // '「申请的安全密钥」',
}
import axios from "axios";
import truck from "@/assets/img/truck .png";
import { AMapManager, lazyAMapApiLoaderInstance } from 'vue-amap'
import {userList} from "../../api/userMG";
let amapManager = new AMapManager()
export default {
  name: "AMap",
  data() {
    let self = this
    return {
      // 地图需要的
      searchValue: '',
      selectedLocation: null,
      //插件对象
      autocomplete: null,
      placeSearch: null,

      formattedAddress:null,//转化后的地址位置parseAddress对外包的接口
      currentLocation: null, // 用于保存当前位置的经纬度信息
      truck:truck,
      map: null,
      marker: null,
      markers: [], // 定义一个数组来保存 Marker
      infoWindow: {//信息窗口
        isShow: false,
        content: '',
        position: []
      },
      // 高德地图插件全局变量
      geocoder: null,
      //axious加载的数据
      trackInfoList: [],//车辆信息
    // el-autocomplete需要的
      timeout:  null,
      loading: false, //是显示加载
      licensePlate: '',//实时绑定的数据
      accidentLicensePlate:'',
    //   车辆位置动态变化
      carMarker: null,
      pathCoordinates: [],
      currentPathIndex: 0,
      totalDistance: 0,//起点到终点之间的距离


    // el-dialog组件的数据
      title: '车辆事故报警',
      alarmVisible: true, //控制警报页面显示与隐藏
    }
  },
  created() {
    // 在组件创建时，发起向后端请求最新的车辆位置数据
    this.trackList()
    // 然后每隔一定时间，重新获取一次车辆位置数据
    setInterval(() => {
      this.dynamicTrackInfo()
    }, 10000)
    setInterval(() => {
      this.trackList()
    }, 9000)
  },
  mounted() {

    this.initMap()
    this.initplugins();

    setTimeout(() => {
      // 这里是延迟调用的回调函数
      this.dynamicTrackInfo();
    }, 1000)

  },
  methods: {


    initMap() {
      const that = this    // 获取当前位置
      navigator.geolocation.getCurrentPosition(position => {
        this.currentLocation = [
          position.coords.longitude,
          position.coords.latitude
        ]
      });
      this.map = new AMap.Map('container', {
        center: this.currentLocation,
        zoom: 18,
      })
      //添加高德自带的组件
      AMap.plugin(['AMap.Scale','AMap.ToolBar','AMap.Geolocation','AMap.OverView','AMap.MapType','AMap.PlaceSearch'], () => {
        // 在回调函数中进行插件的初始化
        const scale = new AMap.Scale();
        const toolBar = new AMap.ToolBar();
        const geolocation = new AMap.Geolocation({});
        const overView = new AMap.OverView({
          isOpen: true,
          visible: true
        });
        const mapType = new AMap.MapType();
        // 初始化地点搜索服务
        const placeSearch = new AMap.PlaceSearch({
          pageSize: 5,
          pageIndex: 1,
          city: '北京市',
          map:that.map
        });
        this.map.addControl(scale);
        this.map.addControl(toolBar);
        this.map.addControl(geolocation);
        this.map.addControl(overView);
        this.map.addControl(mapType);
        this.map.addControl(placeSearch);
      });
    },
    // 高德地图插件初始化
    initplugins(){
      this.geocoder = new AMap.Geocoder({
        city: '全国'
      });
    },
    // 车辆动态位置显示
    dynamicTrackInfo(){

      // 首先清空所有marker
      for (let i = 0; i < this.markers.length; i++) {
        this.markers[i].setMap(null);
      }
      this.markers = []; // 清空 markers 数组

      console.log("刷新车辆在地图上面的位置")
      const that= this
      this.trackInfoList.forEach(item => {
        if (item.longitude == 0 || !item.longitude) {
          return
        }
        // 给trackInfoList对象添加属性position-------

        item.position = [item.longitude, item.latitude];
        if (item.status==2){//但检查到车辆存在问题时
          this.accidentLicensePlate=item.license_plate
          this.open4()
        //   this.map.setCenter(item.position)
        //   this.map.setZoom(18)//设置尺度
        }
        // 数据不同步？？？？？
        this.geocoder.getAddress(item.position, (status, result) => {
          console.log("item.position:"+item.position)
          if (status === 'complete' && result.info === 'OK') {
            item.currentPosition = result.regeocode.formattedAddress
            console.log("item.currentPosition:"+item.currentPosition)
          } else {
            console.log("报错："+result);
          }
        });
         this.marker = new AMap.Marker({
          map: this.map,
          position: [item.longitude, item.latitude],
          icon: require('../../assets/img/truck .png'),
          offset: new AMap.Pixel(-13, -30)
        })
        this.markers.push(this.marker);

        that.marker.on('click', function () {
          //构建信息窗体中显示的内容
          var info = [];
          info.push("车牌号:" + item.license_plate);
          info.push("司机名:" + item.username);
          info.push("手机号:" + item.phone);
          if (item.status==0){
            info.push("状态:停止中");
          }else if(item.status==1){
            info.push("状态:驾驶中");
          }else {
            info.push("<span class='error-text'>状态:故障中</span>"); // 将"状态:故障中"用span标签包裹，并添加一个自定义class
          }
          info.push("地址:"+item.currentPosition);
          that.infoWindow = new AMap.InfoWindow({
            content: info.join("<br/>"), //使用默认信息窗体框样式，显示信息内容
          });
          //鼠标点击marker弹出自定义的信息窗体
          that.infoWindow.open(that.map, item.position);
        });

        //   path 是驾车导航的起、途径和终点，最多支持16个途经点
        var path = []

        path.push([126.656681, 45.795415]) //起点
        path.push(item.position)//当前位置
        // this.map.plugin()

        // this.map.plugin('AMap.DragRoute', function () {
        //   var route = new AMap.DragRoute(that.map, path, AMap.DrivingPolicy.LEAST_FEE)
        //   // 查询导航路径并开启拖拽导航
        //   route.search()
        // })


      })
    },


    //el-autocomplete自动补全功能
    querySearchAsync(queryString, cb) {
      var trackInfoList = this.trackInfoList;
      var results = queryString ? trackInfoList.filter(this.createStateFilter(queryString)) : trackInfoList;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 3000 * Math.random());
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
      if (item.position) {
        this.map.setCenter(item.position)
        this.map.setZoom(18)//设置尺度
        // this.$refs.amap.setCenter(item.position);
      }
    },
    // 点击查询
    searchAddress(){
      this.trackInfoList.forEach(item => {
        if (this.licensePlate == item.license_plate){
          this.map.setCenter(item.position)
          this.map.setZoom(18)//设置尺度
        }
      })
    },

  //   trackList网络请求加载数据
    trackList(){
      console.log("后端数据刷新！！！")
      var that =this
      axios({
        method: 'post',
        url: '/Track/get_track_info_list',
      }).then((response) => {
        if (response.data != null){
          that.trackInfoList=response.data
        }else{
          console.log("提取失败！")
          //失败
          this.$message({
            type: 'error',
            message: '提取失败！'
          })
        }
      })
    },
    // 地址解析
    parseAddress(longitude, latitude) {
      // const geocoder = new AMap.Geocoder({
      //   city: '全国'
      // });
      const point = [longitude,latitude];
        this.geocoder.getAddress(point, (status, result) => {
          if (status === 'complete' && result.info === 'OK') {
            this.formattedAddress = result.regeocode.formattedAddress
            console.log("解析以后的地址this.formattedAddress："+this.formattedAddress)
          } else {
            console.log("报错："+result);
          }
        });
    },
    searchInput(queryString, cb) {
      if (queryString !== '') {
        AMap.plugin('AMap.Autocomplete', () => {
          const autoOptions = {
            city: '杭州市',
          };
          const autoComplete = new AMap.Autocomplete(autoOptions);
          autoComplete.search(queryString, (status, result) => {
            if (result) {
              const matchList = result.tips.map(item => {
                return {
                  value: item.name,
                  location: item.location,
                };
              });
              cb(matchList);
            }
          });
        });
      } else {
        cb([]);
      }
    },
    selectLocation(selected) {
      // this.selectedLocation:119.825478,30.291076
      this.selectedLocation = selected.location;

    },
    goToSelectedLocation() {
      console.log("this.selectedLocation:"+this.selectedLocation)
      if (!this.selectedLocation) {
        console.log("this.selectedLocation有错误")
        return;
      }

      console.log("解析以后的地址："+this.searchValue)
      this.map.setZoom(18)
      this.map.panTo(this.selectedLocation);
      this.searchValue=this.parseAddress(longitude,latitude)
    },


    // el-dialog组件的方法
    // 关闭编辑、增加弹出框
    // closeDialog(dialog) {
    //   if (dialog == 'alarm') {
    //     this.alarmVisible = false
    //   }
    // },
    // Message 消息提示
    open4() {
      this.$message({
        showClose: true,
        message: '车牌号码为:'+this.accidentLicensePlate+"车辆发生车祸！！！",
        type: 'error'
      });
    }

    }
}
</script>

<style lang="css">
.container {
  width: 100%;
  height: 100%;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}
.search-box {
  position: absolute;
  z-index: 5;
  width: 80%;
  left: 5%;
  top: 10px;
  height: 30px;
}
.search-box2 {
  position: absolute;
  z-index: 5;
  width: 30%;
  left: 65%;
  top: 10px;
  height: 30px;
}
.tip-box {
  width: 100%;
  max-height: 260px;
  position: absolute;
  top: 30px;
  overflow-y: auto;
  background-color: #fff;
}

.user-search {
  margin-top: 20px;
}
/*清除左下角的高德地图的logo*/
.amap-logo {
  display: none;
  opacity: 0 !important;
}
.amap-copyright {
  opacity: 0;
}

.error-text {
  color: red;
}
</style>



