<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>视频回放</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-select size="small" v-model="formInline.deptName" placeholder="请选择">
          <el-option label="全部" value=""></el-option>
          <el-option v-for="(item, index) in deptNameInfoList" :key="index" :value="item">{{item}}</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.phone" placeholder="输入手机号"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.license_plate" placeholder="输入车牌号"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
<!--        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()">添加</el-button>-->
<!--        <el-button size="small" type="primary" @click="handleunit()">部门设置</el-button>-->
      </el-form-item>
    </el-form>

<!--    <div style="width: 298px;text-align: center">-->
<!--      <video style="width:950px" poster="https://cdn.jsdelivr.net/gh/themusecatcher/resources@0.0.3/bao.jpg" controls>-->
<!--      <video style="width:950px" controls>-->
<!--        &lt;!&ndash;            <source src="../../assets/img/1.mp4" type="video/mp4" >&ndash;&gt;-->
<!--        <source :src="videoUrl" type="video/mp4">-->
<!--      </video>-->
    <video style="width:950px" controls v-bind:src="videoUrl"></video>

    <p>
      <el-tag>事故视频详细列表</el-tag>
    </p>
    <!--列表-->
    <el-table size="small" @selection-change="selectChange" :data="visibleVideoInfoList" highlight-current-row v-loading="loading" border element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="selection" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="videoName" label="视频名" min-width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="公司" width="80">
      </el-table-column>
      <el-table-column align="center" sortable prop="username" label="用户名" width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="phone" label="手机号" width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="license_plate" label="车牌号" width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="starttime" label="录制时间" min-width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="duration" label="时长" min-width="80">
      </el-table-column>
      <el-table-column align="center" sortable prop="accidentAddress" label="事故地点" min-width="100">
      </el-table-column>
      <el-table-column align="center" sortable prop="type" label="类型" min-width="80">
        <template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.type == 1">交通事故</el-tag>
          <el-tag v-if="scope.row.type == 2">其他</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="300">
        <template slot-scope="scope">
<!--          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>-->
<!--          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)">删除</el-button>-->
<!--            <el-button size="mini" type="success" :loading="scope.row.loadin0g" @click="startPlay(scope.$index, scope.row)">
          {{ scope.row.loading ? '正在播放' : '播放' }}
        </el-button>  -->
          <el-button v-if="scope.$index==currentIndex" size="mini" type="danger" @click="startPlay(scope.$index, scope.row)">正在播放</el-button>
          <el-button v-else size="mini" type="success" @click="startPlay(scope.$index, scope.row)">播放</el-button>
          <el-button size="mini" type="success" @click="download(scope.$index, scope.row)">下载</el-button>
        </template>
      </el-table-column>

    </el-table>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="currentVideoInfoList.length">
    </el-pagination>

    <!-- 分页组件 -->
<!--    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>-->
<!--    v-bind:child-msg="pageparm": 这是Vue.js的属性绑定语法。v-bind或简写为:用于绑定HTML属性。在这个例子中，它绑定了一个名为child-msg的属性，并且其值是pageparm。pageparm应该是在Vue实例中定义的一个变量或者表达式。
@callFather="callFather": 这是Vue.js的事件绑定语法。@用于监听DOM事件。在这个例子中，它监听了名为callFather的事件，当这个事件触发时，会执行Vue实例中的callFather方法。-->

  </div>
</template>


<script>
import Pagination from '../../components/Pagination'
import axios from "axios";
import index from "vuex";

export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      loading: false, //是显示加载
      currentIndex:-1,
      // 请求数据参数
      formInline: {
        deptName: '',//给出的限制条件
        phone: '',
        license_plate: '',
      },
      videoInfoList:[],
      currentVideoInfoList:[],
      deptNameInfoList:[],
      videoUrl:'http://101.34.13.144:8080/examples/2023-11-08 20-04-27_18345264895.mov' ,
    }

  },
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */
  watch: {},

  /**
   * 创建完毕
   */
  created() {
    this.videoList()
    this.deptNameList()
  },

  computed: {
    index() {
      return index
    },
    //模拟分页查询
    visibleVideoInfoList() {

      let result = this.videoInfoList;
      // const startIndex = (this.currentPage - 1) * this.pageSize;
      // const endIndex = this.currentPage * this.pageSize;
      // let result = this.videoInfoList.slice(startIndex, endIndex);

      if (this.formInline.deptName) {
        // 根据部门名称进行筛选
        result = result.filter(video => video.deptName === this.formInline.deptName);
      }
      if (this.formInline.phone) {
        // 如果手机号不为空，则根据手机号进行筛选
        result = result.filter(video => video.phone === this.formInline.phone);
      }
      if (this.formInline.license_plate) {
        // 如果车牌号不为空，则根据车牌号进行筛选
        result = result.filter(video => video.license_plate === this.formInline.license_plate);
      }

      this.currentVideoInfoList=result;
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = this.currentPage * this.pageSize;
      return result.slice(startIndex, endIndex);
    }
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },

    // 点击播放
    startPlay: function(index, row) {

      this.currentIndex=index
      this.videoUrl=row.videoUrl
      console.log("this.videoUrl:"+this.videoUrl)
    },
    // 点击下载
    download:function (index, row) {
      // 使用原生方式重定向到下载链接
      window.location.href = row.videoUrl;
    },

    deptNameList(){
      this.videoInfoList.forEach(obj => {
        if(!this.deptNameInfoList.includes(obj.deptName)) {
          this.deptNameInfoList.push(obj.deptName);
        }
      });
      console.log("this.deptNameInfoList.length"+this.deptNameInfoList.length+"this.deptNameInfoList"+this.deptNameInfoList[0])
    },
    videoList(){
      console.log("videoList()后端数据刷新！！！")
      this.loading = true
      const that = this;
      axios({
        method: 'post',
        url: '/Track/get_video_info_list',
      }).then((response) => {
        if (response.data != null){
          that.videoInfoList=response.data
          console.log("response.data:"+response.data)
          console.log("response.data.length():"+response.data.length)
          that.deptNameList();
        }else{
          console.log("提取失败！")
          //失败
          this.$message({
            type: 'error',
            message: '提取失败！'
          })
        }
      })
      this.loading = false
    },
    // 选择复选框事件
    selectChange(val) {
      this.selectdata = val
    },
    //搜索事件
    search() {
      console.log("formInline:"+this.formInline.deptName+":"+this.formInline.phone+":"+this.formInline.license_plate)


    },
  }
}
</script>


<style scoped>
.user-search {
  margin-top: 20px;
}

.userRole {
  width: 100%;
}
</style>



